import {
  type FieldPathMap,
} from './integration-validation.types.js';

import {
  INT1532_TO_INT1531_FieldPathMap
} from '../../integration-pair/source.int15-3-2.dest.int15-3-1.map.rules.js';

import {
    INT332_TO_INT331_FieldPathMap
} from '../../integration-pair/source.int33-2.dest.int33-1.map.rules.js';

import {
    INT322_TO_INT321_FieldPathMap
} from '../../integration-pair/source.int32-2.dest.int32-1.map.rules.js';

import {
  INT16_TO_INT17_FieldPathMap
} from '../../integration-pair/source.int16.dest.int17.map.rules.js';

import{
  INT191_TO_INT20_FieldPathMap
} from '../../integration-pair/source.int19-1.dest.int20.map.rules.js';

import{
  INT192_TO_INT20_FieldPathMap
} from '../../integration-pair/source.int19-2.dest.int20.map.rules.js';

import{
  INT193_TO_INT20_FieldPathMap
} from '../../integration-pair/source.int19-3.dest.int20.map.rules.js';

import {
    INT031_TO_INT04_FieldPathMap
} from '../../integration-pair/source.int03-1.dest.int04.map.rules.js';
import {
    INT032_TO_INT04_FieldPathMap
} from '../../integration-pair/source.int03-2.dest.int04.map.rules.js';
import { INT04_TO_INT31_FieldPathMap } from '../../integration-pair/source.int04.dest.int31.map.rules.js';
import { INT04_TO_INT1511_FieldPathMap } from '../../integration-pair/source.int04.dest.int1511.map.rules.js';
import { INT122_TO_INT121_FieldPathMap } from '../../integration-pair/source.int12-2.dest.int12-1.map.rules.js';
import { INT16_TO_INT20_FieldPathMap } from '../../integration-pair/source.int16.dest.int20.map.rules.js';


export function resolveFieldPathMap(source: string, dest: string): FieldPathMap | null {
  if (source === 'int15-3-2' && dest === 'int15-3-1') {
    return INT1532_TO_INT1531_FieldPathMap;
  } 

  if (source === 'int33-2' && dest === 'int33-1') {
    return INT332_TO_INT331_FieldPathMap;
  } 
  if(source === 'int32-2' && dest === 'int32-1'){
    return INT322_TO_INT321_FieldPathMap;
  }
  if(source === 'int16' && dest === 'int17'){
    return INT16_TO_INT17_FieldPathMap
  }
  if(source === 'int19-1' && dest === 'int20'){
    return INT191_TO_INT20_FieldPathMap;
  }
  if(source === 'int19-2' && dest === 'int20'){
    return INT192_TO_INT20_FieldPathMap;
  }
  if(source === 'int19-3' && dest === 'int20'){
    return INT193_TO_INT20_FieldPathMap;
  }
  if(source === 'int03-2' && dest === 'int04'){
    return INT032_TO_INT04_FieldPathMap;
  }
  if(source === 'int03-1' && dest === 'int04'){
    return INT031_TO_INT04_FieldPathMap;
  }
  if(source === 'int04' && dest === 'int31'){
    return INT04_TO_INT31_FieldPathMap;
  }
  if(source === 'int12-2' && dest === 'int12-1'){
    return INT122_TO_INT121_FieldPathMap;
  }
  if(source === 'int04' && dest === 'int15-1-1'){
    return INT04_TO_INT1511_FieldPathMap;
  }
  if(source === 'int16' && dest === 'int20'){
    return INT16_TO_INT20_FieldPathMap;
  }
  return null;
}